import React,{Component} from 'react'

class PacmanImageMenu extends Component{
    render(){
        return(
            <div className="row">
                <div><img src="images/pacman.png" alt="loadingPacman" /></div>
            </div>
        );
    }
}

export default PacmanImageMenu;


